import React from "react";
import styled from "styled-components";

const SectionStyled = styled.section`
padding-top: 5vh;
padding-bottom: 5vh;
@media (max-width: 968px){
padding-top: 3vh;
padding-bottom: 3vh;
}
`;

const Section = (props) => {
  return (
    <SectionStyled {...props}>
      {props.children}
    </SectionStyled>
  )
}

export default Section;
