import React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import styled, { keyframes } from "styled-components"

const animation = keyframes`
  0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}
  100%{-webkit-transform:rotate(-359deg);transform:rotate(-359deg)}
`

const Icon = styled.div`
position: relative;
&:hover {
    animation: ${animation} 3s ease-in-out alternate;
      animation-iteration-count: 1;
  }
`;

const Skill = ({ icon, title, description }) => {
  return (
    <div
      className="mt-3 p-4 bg-white"
      style={{ borderRadius: "20px 20px 20px 20px" }}
    >
      <div className="text-center text-secondary">
        <Icon><FontAwesomeIcon icon={icon} size="8x" className="p-3" fixedWidth /></Icon>
        <h3>{title}</h3>
      </div>

      <p
        className="mt-3 font-weight-light text-center"
        dangerouslySetInnerHTML={{ __html: description }}
      />
    </div>
  )
}

export default Skill
