import React from "react"
import styled, { keyframes } from "styled-components"
import {Badge, Container, Row, Col} from "reactstrap"
const animation = keyframes`
  from {
    top: 0;
  }

  to {
    top: -15px;
  }
`
const Img = styled.img`
  position: relative;
  &:hover {
    animation: ${animation} 2s linear infinite alternate;
  }
`
const Reason = ({ index, reason }) => {
  const ReasonStyled = styled.div`
    margin-top: 2vh;
    margin-bottom: 2vh;
    padding: 30px;
  `

  if(index%2===1) {
    return (
      <div>
        <ReasonStyled>
          <Row>
            <Col md={2}/>
            <Col md={4} className="align-self-center text-right">
              <h4 className="text-primary fa-2x">{reason.title}</h4>
              <p className="text-primary lead">
                {reason.list.map((item, i) => (
                    <span key={i}>{item}<br/></span>
                ))}
              </p>
            </Col>
            <Col md={4} className="align-self-center">
              <Img src={reason.image} className="img-fluid" alt=""/>
            </Col>
          </Row>
        </ReasonStyled>
      </div>
    )
  }

  return (
    <div>
      <ReasonStyled>
        <Row>
          <Col md={2}></Col>
          <Col md={4} className="align-self-center">
            <Img src={reason.image} className="img-fluid" alt=""/>
          </Col>
          <Col md={4} className="align-self-center">
            <h4 className="text-primary fa-2x">{reason.title}</h4>
            <p className="text-primary lead">
              {reason.list.map((item, i) => (
                <span key={i}>{item}<br/></span>
              ))}
            </p>
          </Col>
        </Row>
      </ReasonStyled>
    </div>
  )
}

export default Reason
